@use "library";
@use "index";

.calculater {
    max-width: 40%;
    
    @media screen and (max-width: 900px) {
        max-width: 48%;
    };

    @media screen and (max-width: 690px) {
        max-width: 50%;
    };

    @media screen and (max-width: 570px) {
        max-width: 60%;
    };

    @media screen and (max-width: 400px) {
        max-width: 75%;
    };
    
}

